            if (parentId == data[i].id){

                console.log(data[i])

                vm.dataList.push(data[i]);

                vm.findById(vm.ztreeDataSource, data[i].parentId)

                return data[i]

            }



            if (data[i].hasOwnProperty('children')){

                vm.findById(data[i].children,parentId)

            }



        }
    },
